import Web3 from 'web3';
import getRpcUrl from '@/utils/getRpcUrl';

const RPC_URL = getRpcUrl();

console.log(RPC_URL, 'RPC_URL');

let web3;
if (window.ethereum) {
    web3 = new Web3(window.ethereum);
    console.log(">>>>>>>>>>>>>>>>>>>>>>>>> simpleRpcProvider from window.ethereum ");
} else {
    console.log(">>>>>>>>>>>>>>>>>>>>>>>>> simpleRpcProvider from window.ethereum  RPC_URL: ", RPC_URL);
    web3 = new Web3(new Web3.providers.HttpProvider(RPC_URL));
}

export const simpleRpcProvider = web3;

export default null;
