import axios from 'axios';
import qs from 'qs';

export const BASEURL = axios.defaults.baseURL = 'https://api.paths.finance';
// export const BASEURL = axios.defaults.baseURL = 'https://api-test.paths.finance/';

export const get = (url, params = {}) => {
    return new Promise((resolve, reject) => {
        axios.get(url, {params: params})
            .then(response => {
                resolve(response.data);
            })
            .catch(err => {
                reject(err);
            })
    })
}

export const post = (url, data = {}) => {
    return new Promise((resolve, reject) => {

        axios.post(url, qs.stringify(data, { arrayFormat: 'repeat' }),{headers: {'Content-Type': 'application/json'}})
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}

export const postConfig = (url, data = {}, config) => {
    return new Promise((resolve, reject) => {
        axios.post(url, data, config)
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}