import { BASEURL, get, post, postConfig } from './http.js';

const BSC_RPC = 'https://bsc-dataseed2.ninicoin.io/';
const ETH_RPC = 'https://eth.rpc.blxrbdn.com/';

// 获取Token列表
export const getBaseInfo = data => get(BASEURL + '/api/getBaseInfo', data);
// 查询价格
export const multiQuote = data => get(BASEURL + '/api/multiQuote', data);
// 发起交易
export const commonSwap = data => {
    let config = {
        headers: {'Content-Type': 'application/json'},
        redirect: 'follow'
    };
    return postConfig(BASEURL + '/api/commonSwap', data, config);
}
// 查询余额
export const getBalance = (account, token) => {
    let reqUrl = token.chainName == "BSC" ? BSC_RPC : ETH_RPC;
    let req = {
        "id": 1,
        "jsonrpc": "2.0",
        "method": "eth_getBalance",
        "params":[account.toLowerCase(), "latest"]
    }
    let config = {
        headers: {'Content-Type': 'application/json'},
        redirect: 'follow'
    };
    return postConfig(reqUrl, req, config);
};
// 查询ERC20余额
export const getBalanceErc20 = (account, token) => {
    let req = [];
    let id = 1;
    let param1 = {
        "data": '0x70a08231000000000000000000000000' + account.slice(2).toLowerCase(),
        "from": account,
        "to": token.address
    };
    req.push({
        "id": id,
        "jsonrpc": "2.0",
        "method": "eth_call",
        "params":[param1, "latest"]
    })
    let config = {
        headers: {'Content-Type': 'application/json'},
        // redirect: 'follow'
    };
    console.log(">>>>>>>>>> getBalanceErc20 req:", JSON.stringify(req));
    let reqUrl = token.chainName == "BSC" ? BSC_RPC : ETH_RPC;
    return postConfig(reqUrl, req, config);
}

